.header-nav {
	margin-bottom: -4px;
	&__list {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		flex-wrap: wrap;
	}
	&__item {
		margin: 0 15px;
		&:first-child {
			margin-left: 0;
		}
		&:last-child {
			margin-right: 0;
		}
	}
	&__link {
		display: block;
		text-transform: uppercase;
		text-decoration: none;
		.dinpro();
		.font-size(12);
		letter-spacing: 1px;
		color: #000000;
		position: relative;
		&:after {
			content: "";
			position: absolute;
			width: 100%;
			display: block;
			height: 2px;
			background: transparent;
			bottom: -2px;
			transition: all .2s ease;
			opacity: 0;
		}
		&:hover {
			color: #ff3a1e;
			&:after {
				opacity: 1;
				background: #ff3a1e;
			}
		}
	}
}