.section {
	&_slider {
		margin-bottom: 28px;
	}
	&_main-content {
		margin-bottom: 63px;
	}

	&__inner-wrap {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
	}
	&__col {
		&_main {
			flex-grow: 20;
			margin-right: 28px;
		}
		&_aside {
			width: 280px;
		}
	}
}


@media screen and (max-width: 1024px) {
	.section {
		&__inner-wrap {
			flex-direction: column;
		}
		&__col {
			&_main {
				width: 100%;
				flex-grow: 0;
				margin-right: 0;
				margin-bottom: 30px;
			}
			&_aside {
				width: 100%;
			}
		}
	}
}

@media screen and (max-width: 800px) {
	.section {
		&_slider {
			> .container {
				padding: 0;
			}
		}
	}
}