.form-control {
	&__field {
		margin-bottom: 13px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__label {
		.dinpro-m();
		.font-size(10);
		letter-spacing: 0.5px;
		color: rgba(62,62,62,.8);
		text-transform: uppercase;
		line-height: normal;
		margin-bottom: 3px;
		display: inline-block;
		vertical-align: bottom;
	}
	&__input-holder {
		&_required {
			position: relative;
			.form-control__input {
				padding-right: 25px;
			}
			&:after {
				content: "*";
				position: absolute;
				right: 0;
				top: 0;
				line-height: 40px;
				z-index: 1;
				.regular();
				.font-size(22);
				color: #ea461b;
				text-align: left;
				width: 20px;
				height: 100%;
			}
		}
	}
	&__input {
		appearance: none;
		margin: 0;
		padding: 0;
		border-radius: 0;
		border: 0;
		box-shadow: none;
		background: none;

		.regular();
		.font-size(14);
		color: rgba(43,43,43,.7);
		width: 100%;
		padding: 0 13px;
		height: 35px;
		background: #f4f4f4;
		border: 2px solid #f4f4f4;
		transition: all .2s ease;

		&:focus {
			background: #e9e9e9;
			border-color: #e9e9e9;
		}

		&::-webkit-input-placeholder {
			color: #2c2724;
			opacity: 1 !important;
		};
			&::-moz-placeholder {
			color: #2c2724 !important;
			opacity: 1 !important;
		};
			&:-ms-input-placeholder {
			color: #2c2724;
			opacity: 1 !important;
		};
	}
	&__submit {
		appearance: none;
		box-shadow: none;
		border: 0;
		cursor: pointer;
	}

	&_donate-block {
		.form-control__input {
			height: 30px;
		}
		.checkbox {
			margin: 20px 0;
		}
		.checkbox__input-holder {
			margin-right: 7px;
			width: 15px;
			height: 15px;
		}
		.checkbox__icon {
			border-width: 1px;
		}
		.checkbox__text {
			.font-size(10);
		}
		.btn {
			.font-size(11);
			letter-spacing: normal;
		}
	}
}

.checkbox {
	cursor: pointer;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	&__input-holder {
		display: block;
		position: relative;
		width: 18px;
		height: 18px;
		flex-shrink: 0;
		margin-right: 12px;
	}
	&__input {
		appearance: none;
		margin: 0;
		padding: 0;
		border-radius: 0;
		border: 0;
		box-shadow: none;
		background: none;

		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;
		visibility: hidden;

		&:checked + span {
			border-color: #ff3a1e;
			&:after {
				opacity: 1;
				visibility: visible;
			}
		}
	}
	&__icon {
		display: block;
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		background: #f4f4f4;
		border: 2px solid #f4f4f4;
		transition: all .2s ease;
		&:after {
			content: "";
			display: block;
			.svg-icon('icon-check', 10px, 8px);
			opacity: 0;
			visibility: hidden;
			transition: all .2s ease;
			position: absolute;
			top: 50%;
			left: 50%;
			margin-top: -4px;
			margin-left: -5px;
		}
	}
	&__text {
		user-select: none;
		.regular();
		.font-size(14);
		color: #535353;
		line-height: 1.2;
	}
}