.donate-block {
	border: 1px solid #d7d7d7;
	padding: 27px 30px 33px;
	margin-bottom: 40px;
	&__title {
		.dinpro();
		.font-size(17);
		line-height: 1.24;
		color: #ff3a1e;
		margin-bottom: 20px;
		text-transform: uppercase;
	}
}

@media screen and (max-width: 1024px) {
	.donate-block {
		width: 100%;
	}
}

@media screen and (max-width: 500px) {
	.donate-block {
		padding: 27px 20px 33px;
		.btn {
			min-width: 0;
			width: 100%;
		}
	}
}