.mobile-menu-wrap {
	display: none;
	position: absolute;
	top: 72px;
	padding: 6px 30px 0;
	left: 0;
	width: 100vw;
	height: calc(~"100vh - 72px");
	z-index: 100;
	background: white;
	overflow: hidden;
	&__inner {
		width: calc(~"100% + 20%");
		height: 100%;
		overflow: scroll;
		background: white;
		-webkit-overflow-scrolling: touch;
	}
	&__head {
		margin-bottom: 70px;
	}
	&__slogan {
		height: 27px;
	}
}