.news-block {
	&__title {
		margin-bottom: 30px;
	}
	&__title-link {
		.dinpro();
		.font-size(17);
		line-height: 1.12;
		text-align: left;
		color: #000000;
		text-decoration: none;
		text-transform: uppercase;
	}
	&__date {
		.regular();
		.font-size(12);
		text-align: left;
		color: rgba(0,0,0,.5);
		margin-left: 10px - 4px;
	}
	&__content {
		margin-bottom: 20px;
	}
	&__footer {}
	&__all-news {
		.bold();
		.font-size(15);
		color: #ff3a1e;
		text-decoration: underline;
		&:hover {
			text-decoration: none;
		}
	}
}

@media screen and (max-width: 1024px) {
	.news-block {
		width: calc(~"100% - 280px - 30px");
		margin-right: 30px;
	}
}

@media screen and (max-width: 800px) {
	.news-block {
		width: 100%;
		margin-right: 0;
	}
}