.footer-nav {
	&__list {
		&_sublist {
			margin: 13px 0 30px;
			.footer-nav__item {
				margin-bottom: 0;
			}
			.footer-nav__link {
				text-transform: inherit;
				.regular();
				.font-size(13);
				line-height: 1.54;
				color: #f2f2f2;
			}
		}
	}
	&__item {
		margin-bottom: 6px;
		&:first-child {
			margin-bottom: 0;
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__link {
		text-transform: uppercase;
		.dinpro();
		.font-size(15);
		letter-spacing: 1px;
		line-height: normal;
		color: #ffffff;
		text-decoration: none;
		&:hover {
			text-decoration: underline;
		}
	}
}