.footer {
	background: #a2a7ad;
	&__inner-wrap {
		padding: 43px 0 50px;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		flex-wrap: wrap;
		align-items: flex-start;
		border-bottom: solid 1px #d1d5dc;
	}
	&__col {
		width: 250px;
		margin-right: 160px;
		&_left {}
		&_center {
			flex-grow: 20;
			margin-right: 50px;
		}
		&_right {
			width: auto;
			margin-right: 0;
			padding-left: 20px;
			border-left: solid 1px #d1d5dc;
		}
	}
	&__info {
		.regular();
		.font-size(13);
		color: white;
		line-height: 1.6;
	}
	&__bottom {
		padding: 30px 0;
	}
}


@media screen and (max-width: 940px) {
	.footer {
		&__inner-wrap {
			padding: 43px 0 35px;
		}
		&__col {
			width: 250px;
			margin-right: 100px;
			padding-bottom: 35px;
			&_left {}
			&_center {
				flex-grow: 0;
				margin-right: 0;
			}
			&_right {
				width: 100%;
				padding: 35px 0 0;
				border-left: 0;
				margin-right: 0;
				border-top: solid 1px #d1d5dc;
			}
		}
		&__info {
			.regular();
			.font-size(13);
			color: white;
			line-height: 1.6;
		}
		&__bottom {
			padding: 30px 0;
		}
	}
}

@media screen and (max-width: 800px) {
	.footer {
		&__inner-wrap {
			padding: 43px 0 35px;
			flex-direction: column;
		}
		&__col {
			width: auto;
			margin-right: 0;
			&_left {
				padding-bottom: 0;
			}
		}
	}
}