.helped {
	margin-bottom: 40px;
	&__title {
		margin-bottom: 15px;
	}
	&__title-link {
		.dinpro();
		.font-size(17);
		line-height: 1.24;
		text-align: left;
		color: #000000;
		text-transform: uppercase;
		text-decoration: none;
	}
	&__items {
		margin-bottom: 3px;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: flex-start;
	}
	&__item {
		width: 83px;
		height: 83px;
		background-color: #bbb5b5;
		margin: 0 15px 15px 0;
		&:nth-child(3n) {
			margin-right: 0;
		}
	}
	&__item-link {
		text-decoration: none;
		display: block;
	}
	&__image {
		width: 100%;
		vertical-align: middle;
	}
	&__footer {}
	&__link {
		.dinpro();
		.font-size(15);
		text-align: left;
		color: #ff3a1e;
		&:hover {
			text-decoration: none;
		}
	}
	&__count {}
}


@media screen and (max-width: 1024px) {
	.helped {
		width: 100%;
		&__item {
			&:nth-child(3n) {
				margin-right: 15px;
			}
		}
	}
}

@media screen and (max-width: 500px) {
	.helped {
		&__item {
			width: calc(~"(100% - 15px * 2) / 3");
			height: calc(~"(100% - 15px * 2) / 3");
			// margin: 0 13px 13px 0;
			&:nth-child(3n) {
				margin-right: 0;
			}
		}
	}
}