.mobile-nav-bottom {
	margin-bottom: 50px;
	&__list {}
	&__item {
		margin-bottom: 10px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__link {
		text-decoration: none;
		text-transform: uppercase;
		transition: all .2s ease;
		opacity: 0.56;
		.dinpro();
		.font-size(13);
		line-height: 1.2;
		letter-spacing: 1.8px;
		color: rgba(78,78,78,.56);
		&:hover {
			opacity: 1;
			color: black;
		}
	}
}