.social-menu {
	margin: 30px 0 35px;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	flex-wrap: wrap;
	&__item {
		margin-right: 24px;
		&:last-child {
			margin-right: 0;
		}
	}
	&__link {
		text-decoration: none;
		display: block;
	}

	.icon {
		display: block;
		&.icon-vk {
			.svg-icon('icon-vk', 20px, 20px);
		}
		&.icon-fb {
			.svg-icon('icon-fb', 20px, 20px);
		}
		&.icon-tw {
			.svg-icon('icon-tw', 20px, 20px);
		}
		&.icon-inst {
			.svg-icon('icon-inst', 20px, 20px);
		}
		&.icon-od {
			.svg-icon('icon-od', 20px, 20px);
		}
	}
}