.header-top-nav {
	padding: 16px 0;
	&__list {
		display: flex;
		flex-direction: row;
		justify-content: flex-end;
		align-items: flex-end;
		flex-wrap: wrap;
	}
	&__item {
		margin: 0 15px;
		&:first-child {
			margin-left: 0;
		}
		&:last-child {
			margin-right: 0;
		}
	}
	&__link {
		display: block;
		text-decoration: none;
		text-transform: uppercase;
		.dinpro();
		.font-size(10);
		letter-spacing: 1.4px;
		color: rgba(110,110,110,.56);
		transition: all .2s ease;
		&:hover {
			text-decoration: underline;
		}
	}
}