.mobile-nav {
	position: relative;
	padding-bottom: 30px;
	margin-bottom: 14px;
	&:after {
		content: "";
		position: absolute;
		width: 210px;
		display: block;
		height: 2px;
		background: #ff3a1e;
		bottom: 0;
	}
	&__list {}
	&__item {
		margin-bottom: 14px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__link {
		text-decoration: none;
		text-transform: uppercase;
		.dinpro();
		.font-size(17);
		letter-spacing: 1px;
		color: #000000;
		position: relative;
		&:after {
			content: "";
			position: absolute;
			width: 100%;
			display: block;
			height: 2px;
			background: transparent;
			bottom: -2px;
			transition: all .2s ease;
			opacity: 0;
		}
		&:hover {
			color: #ff3a1e;
			&:after {
				opacity: 1;
				background: #ff3a1e;
			}
		}
	}
}