.program-list {
	&__item {
		height: 443px;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: 50% 50%;
		position: relative;
		&:not(:last-child) {
			margin-bottom: 30px;
		}
	}
	&__label {
		width: 134px;
		height: 47px;
		background-color: #ff3a1e;
		position: absolute;
		top: 26px;
		left: 26px;
		z-index: 2;
		user-select: none;
		.fox();
		.font-size(33);
		line-height: 47px;
		letter-spacing: 2.8px;
		text-align: center;
		color: #ffffff;
	}
	&__link {
		text-decoration: none;
		display: block;
		width: 100%;
		height: 100%;
	}
	&__img-wrap {
		display: none;
		.aspect-ratio(1, 1);
	}
	&__img-holder {}
	&__image {}
}

@media screen and (max-width: 800px) {
	.program-list {
		&__item {
			height: auto;
			background-image: none !important;
			position: relative;
			&:not(:last-child) {
				margin-bottom: 30px;
			}
		}
		&__img-wrap {
			display: block;
			.aspect-ratio(1, 1);
		}
		&__img-holder {}
		&__image {
			vertical-align: middle;
			width: 100%;
		}
	}
}

@media screen and (max-width: 500px) {
	.program-list {
		&__label {
			width: 100px;
			height: 35px;
			top: 15px;
			left: 15px;
			.font-size(23);
			line-height: 37px;
			letter-spacing: 1.3px;
		}
	}
}