.header {
	background: white;
	transition: box-shadow .2s ease;
	&__inner-wrap {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: flex-start;
	}
	&__logo-wrap {
		padding: 34px 40px 34px 0;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		// flex-wrap: wrap;

		.header__slogan {
			display: none;
			align-self: flex-end;
		}
	}
	&__logo-link {
		margin-right: 40px;
		text-decoration: none;
	}
	&__logo {
		max-width: 164px;
		vertical-align: middle;
	}
	&__logo-descr {
		.dinpro();
		.font-size(11);
		opacity: .6;
		text-transform: uppercase;
		line-height: 1.45;
		letter-spacing: 0.8px;
		color: #6a6a6a;
		align-self: center;
	}
	&__nav-wrap {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		flex-grow: 20;
		padding-left: 23px;
		height: 96px;
		position: relative;
		&:before {
			content: "";
			width: 2px;
			height: 100%;
			background: #ff3a1e;
			position: absolute;
			top: 0;
			left: 0;
		}
	}
	&__top {}
	&__bottom {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: baseline;
		flex-wrap: wrap;
	}
	&__slogan {
		max-width: 193px;
	}
	&__menu {
		display: none;
		position: absolute;
		top: 50%;
		right: -5px;
		width: 30px;
		height: 30px;
		z-index: 2;
		margin-top: -15px;
		&:after {
			content: "";
			display: block;
			.svg-icon('icon-close', 18px, 18px);
			position: absolute;
			top: 50%;
			left: 50%;
			margin-top: -9px;
			margin-left: -9px;
			opacity: 0;
			transition: opacity .2s ease;
		}
		// display: none;
		&_active {
			.header__menu-inner {
				opacity: 0;
			}
			&:after {
				opacity: 1;
			}
		}
	}
	&__menu-inner {
		position: absolute;
		top: 0;
		left: 0;
		opacity: 1;
		height: 100%;
		width: 100%;
		transition: opacity .2s ease;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	&__menu-line {
		width: 20px;
		height: 3px;
		background: #ff3a1e;
		margin-bottom: 3px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	.btn {
		display: none;
	}

	&_fixed {
		box-shadow: 6px 6px 15px 0 rgba(0, 0, 0, 0.23);
		width: 100%;
		top: 0;
		left: 0;
		position: fixed;
		z-index: 100;
		.header__logo-wrap {
			padding: 20px 15px 20px 0;
			.header__slogan {
				display: inline;
			}
		}
		.header__logo-link {
			margin-right: 60px;
		}
		.header__logo {
			max-width: 140px;
		}
		.header__logo-descr {
			display: none;
		}
		.header__top {
			display: none;
		}
		.header__slogan {
			display: none;
		}
		.header__nav-wrap {
			height: 60px;
			justify-content: flex-end;
			&:before {
				height: 100%;
			}
		}
		.header__bottom {
			align-items:flex-end;
		}
		.btn {
			display: block;
		}
	}
}




@media screen and (max-width: 1220px) {
	.header {
		&__logo-wrap {
			padding-right: 10px;
		}
		&__logo-descr {
			display: none;
		}

		&_fixed {
			.header__logo-wrap {
				.header__slogan {
					display: none;
				}
			}
			.header__logo-link {
				margin-right: 20px;
			}
		}
	}
}

@media screen and (max-width: 1024px) {
	.header {
		&__logo-wrap {
			padding: 20px 10px 20px 0;
		}
		&__nav-wrap {
			height: 60px;
			justify-content: flex-end;
		}
		&__top {
			display: none;
		}
		&__slogan {
			display: none;
		}

		&_fixed {
			.header__logo-wrap {
				padding: 20px 10px 20px 0;
			}
			.header__logo {
				max-width: 164px;
			}
			.header__logo-link {
				margin-right: 40px;
			}
			.btn {
				display: none;
			}
		}
	}
}

@media screen and (max-width: 800px) {
	.header {
		&__inner-wrap {
			height: 72px;
		}
		&__logo-wrap {
			padding: 0;
			justify-content: center;
			align-items: center;
			height: 100%;
		}
		&__logo-link {
			margin-right: 0;
		}
		&__logo {
			max-width: 127px;
		}
		&__nav-wrap {
			height: 100%;
			padding-left: 0;
			justify-content: center;
			&:before {
				display: none;
			}
		}
		&__bottom {
			display: none;
		}
		&__menu {
			display: block;
		}

		&_fixed {
			.header__logo-wrap {
				padding: 0;
			}
			.header__logo {
				max-width: 127px;
			}
			.header__logo-link {
				margin-right: 0;
			}
			.header__nav-wrap {
				height: 100%;
			}
		}
	}
}