.main-slider {
	height: 262px;
	&__wrap {
		height: 100%;
	}
	&__slide {
		height: 100%;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: 50% 50%;

		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	&__title {
		.fox();
		.font-size(55);
		line-height: 1.33;
		letter-spacing: 0.7px;
		text-align: left;
		color: #ff3a1e;
		position: absolute;
		bottom: 36px;
		align-self: flex-start;
		margin-left: 143px;
	}
	&__arrow {
		position: absolute;
		top: 50%;
		display: block;
		z-index: 2;
		cursor: pointer;
		transition: all .2s ease;
		&_prev {
			left: 45px;
			margin-top: -17px;
			.svg-icon-param("icon-prev", 20px, 34px, "[fill]: #ff3a1e;");
			&:hover {
				.svg-icon-param("icon-prev", 20px, 34px, "[fill]: #d43a24;");
			}
			&:active {
				.svg-icon-param("icon-prev", 20px, 34px, "[fill]: #a22311;");
			}
		}
		&_next {
			right: 45px;
			margin-top: -17px;
			.svg-icon-param("icon-next", 20px, 34px, "[fill]: #ff3a1e;");
			&:hover {
				.svg-icon-param("icon-next", 20px, 34px, "[fill]: #d43a24;");
			}
			&:active {
				.svg-icon-param("icon-next", 20px, 34px, "[fill]: #a22311;");
			}
		}
	}

	.btn {
		align-self: flex-end;
		margin-right: 176px;
	}
}


@media screen and (max-width: 1024px) {
	.main-slider {
		&__slide {
			justify-content: center;
		}
		&__title {
			.font-size(50);
			position: relative;
			bottom: auto;
			text-align: center;
			align-self: center;
			margin: 0 45px 25px;
		}

		.btn {
			align-self: center;
			margin-right: 0;
			order: 2;
		}
	}
}

@media screen and (max-width: 800px) {
	.main-slider {
		&__arrow {
			&_prev {
				left: 10px;
			}
			&_next {
				right: 10px;
			}
		}
	}
}

@media screen and (max-width: 500px) {
	.main-slider {
		&__title {
			.font-size(45);
		}
		&__arrow {
			&_prev {
				left: 10px;
				margin-top: -23px / 2;
				.svg-icon-param("icon-prev", 14px, 23px, "[fill]: #ff3a1e;");
				&:hover {
					.svg-icon-param("icon-prev", 14px, 23px, "[fill]: #d43a24;");
				}
				&:active {
					.svg-icon-param("icon-prev", 14px, 23px, "[fill]: #a22311;");
				}
			}
			&_next {
				right: 10px;
				margin-top: -23px / 2;
				.svg-icon-param("icon-next", 14px, 23px, "[fill]: #ff3a1e;");
				&:hover {
					.svg-icon-param("icon-next", 14px, 23px, "[fill]: #d43a24;");
				}
				&:active {
					.svg-icon-param("icon-next", 14px, 23px, "[fill]: #a22311;");
				}
			}
		}
	}
}