.footer-contacts {
	&__item {
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__link {
		text-decoration: none;

		.icon {
			display: inline-block;
			vertical-align: -2px;
			margin-right: 15px - 4px;
			&.icon-phone {
				.svg-icon('icon-phone', 16px, 16px);
			}
			&.icon-at {
				.svg-icon('icon-at', 16px, 16px);
			}
			&.icon-email {
				.svg-icon('icon-email', 16px, 16px);
			}
			&.icon-organizer {
				.svg-icon('icon-organizer', 16px, 16px);
			}
			&.icon-list {
				.svg-icon('icon-list', 16px, 16px);
			}
		}
	}
	&__link-name {
		.bold();
		.font-size(15);
		text-transform: uppercase;
		letter-spacing: 0.5px;
		line-height: 1.2;
		color: white;
	}
}
